import {Pipe} from '@angular/core';

@Pipe({name: "bool"})
export class BoolPipe {
  public transform(input, _true:string='', _false:string='', unsure:string=''){
  	return (input == null || input==undefined) ? unsure : input ? _true : _false;
  }
}



